@import url("https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap");

/* Minimalist, spacious layout */
.spacious-section {
  padding: 5rem 0 6rem 0;
  margin: 0 auto;
  max-width: 1100px;
}

.card {
  background: #fff;
  border-radius: 1.25rem;
  box-shadow: 0 2px 16px rgba(166,138,113,0.08);
  padding: 2.5rem 2rem;
  margin-bottom: 2.5rem;
  border: 1px solid #f0e6da;
}

.rounded-btn {
  border-radius: 2rem;
  border: 1px solid #a68a71;
  background: transparent;
  color: var(--text);
  padding: 0.75rem 2rem;
  font-family: var(--body-sans);
  font-size: 1.1rem;
  font-weight: 400;
  box-shadow: 0 1px 4px rgba(166,138,113,0.07);
  transition: border 0.2s, box-shadow 0.2s;
  cursor: pointer;
}
.rounded-btn:hover {
  border-color: #7c5a3a;
  box-shadow: 0 2px 8px rgba(166,138,113,0.12);
}

/* Card content blocks for sections */
.card-section {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
}

/* Professional, approachable typography */
h1, h2, h3, h4, h5, h6 {
  font-family: var(--heading-serif);
  color: var(--accent);
  font-weight: 400;
  letter-spacing: 0.5px;
}

p, li, a, span, td, th {
  font-family: var(--body-sans);
  color: var(--text);
  font-size: 1.08rem;
}

/* Add extra white space between major sections */


/* Academic site color palette and font pairing */
:root {
  --background: #f6f1eb;
  --header-gradient: linear-gradient(90deg, #5c4432 0%, #7c5a3a 100%);
  --accent: #a68a71;
  --text: #222;
  --heading-serif: 'Cormorant Garamond', serif;
  --body-sans: 'Montserrat', sans-serif;
}

body {
  background: var(--background);
  color: var(--text);
  font-family: var(--body-sans);
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--heading-serif);
  color: var(--accent);
  letter-spacing: 0.5px;
}

p, li, a, span, td, th {
  font-family: var(--body-sans);
  color: var(--text);
}

a {
  color: var(--accent);
  text-decoration: none;
}

a:hover {
  color: #7c5a3a;
}

header, nav {
  background: var(--header-gradient);
  color: #fff;
  box-shadow: 0 2px 8px rgba(92,68,50,0.08);
}

/* Accent for headings */
h2, h3 {
  color: var(--accent);
}

/* Section backgrounds */
section {
  background: var(--background);
}
/**
* CSS file to hold styles that apply across the application
*/

div#main {
  display: flex;
  flex-direction: column;
  width: 100vw;
}

section {
  position: relative;
  width: 100vw;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  height: 100%;
  left: 50%;
  object-fit: cover;
  pointer-events: none;
  position: absolute;
  top: 0;
  transform: translate(-50%, 0);
  width: 100vmax;
  z-index: -1;
}

h1 {
  font-family: "Cormorant Garamond", serif;
  font-size: 7rem;
  font-weight: 400;
  line-height: 0.8;
  margin: 0;
}

h2 {
  font-size: 2rem;
  font-weight: 400;
  margin: 0;
  padding-top: 1rem;
}

h3 {
  font-weight: 400;
  font-size: 1.25rem;
  margin: 0;
}

p {
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 0;
  font-weight: 300;
}

p.large {
  font-size: 24px;
}

p.small {
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #4e567e;
}

a {
  color: black;
  text-decoration: none;
}

a:hover {
  color: #4e567e;
}

.container {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 330px 300px;
  margin: 20px auto;
}

.container .box {
  border-radius: 10px;
  border: 1px solid rgb(204, 204, 204);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  text-align: center;
}

img.socialIcon {
  height: 30px;
  width: 30px;
}

/* Portfolio carousel image styles for uniform size and left margin */
.portfolio-carousel-img {
  width: 100%;
  max-width: 400px;
  height: 300px;
  object-fit: cover;
  border-radius: 1rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  margin: 0 auto 0 2rem;
  display: block;
}

/* Fix rotation for poc2.jpg in carousel */
.portfolio-carousel-img.poc2 {
  transform: none !important;
  rotate: none !important;
}
